import EventsService from './services/EventsService';
import getParameterByName from 'util/getParameterByName';

var eventCalendarSettings = {
    // all types selected by default,
    ALL_EVENT_TYPES: [
        'Destination Seminar', 'Regional Course', 'Complimentary Event', 'Tradeshow'
    ],
    selectedTypes: []
};

function sortEventList( $el ) {
    var sortType = $el.attr( 'data-sortby' ),
        dir = parseInt( $el.attr( 'data-sortorder' ) ) || 1,
        elmList,
        comparator;

    elmList = $( '#eventQuickFindList .' + sortType );
    switch ( sortType ) {
        case 'courseTitle':
        case 'location':
            comparator = function ( a, b ) {
                return $( a ).text() > $( b ).text()
                    ? 1 * dir
                    : -1 * dir;
            };
            break;
        case 'date':
            comparator = function ( a, b ) {
                return parseDate( $( a ).attr( 'data-datetime' ) ) > parseDate( $( b ).attr( 'data-datetime' ) )
                    ? 1 * dir
                    : -1 * dir;
            };
            break;
        default:
            return false;
    }

    // toggle sort order
    $el.attr( 'data-sortorder', ( dir === 1 )
        ? -1
        : 1 );
    // sort elements and place in DOM
    elmList.sortElements( comparator, getParent );
    $( document ).trigger( 'menu:sort', $el );

    function parseDate( dateStr ) {
        var parts = dateStr.split( '-' );
        return new Date( parts[0], parts[1] - 1, parts[2] );
    }

    function getParent() {
        return $( this ).parents( 'li' );
    }
}

function handleEventSort( e ) {
    var $el = $( e.currentTarget );

    sortEventList( $el );
}

$( document ).ready( function () {
    // create events dropdown and add sort handler
    var $dropdown = $( '#page-content .dropDownSelect' );
    $dropdown.dropDownSelect().on( 'click', '.header li', handleEventSort );
    $( document ).on( 'menu:sort', function ( e, el ) {
        var current = $( el ).addClass( 'active' );
        //reset sort order for non-active menu headings
        current.siblings( 'li' ).each( function () {
            if ( this !== el ) {
                $( this ).attr( 'data-sortorder', 1 ).removeClass( 'active' );
            }
        } );
    } );

    sortEventList( $dropdown.find( '.header li:first' ) );

    // initialize podcast subscriptionl links
    $( 'a.itunes' ).on( 'click', function ( e ) {
        e.preventDefault();
        // send to itmsOpen() to open store page in itunes
        //http://phobos.apple.com/WebObjects/MZStore.woa/wa/viewPodcast?id=159270307
        // send this value to itunes.detect method
        //http://itunes.apple.com/us/podcast/ultradent-products-inc.-podcast/id159270307
        // this will attempt to open podcast page in itunes store (currently fails, i believe upi is not registered with itunes store)
        //itms://ax.itunes.apple.com/WebObjects/MZStore.woa/wa/viewSoftware?id=159270307
        //pass this to windlow.loction to add subscription to itunes
        //itpc://podcast.ultradent.com/podcast.xml
        //

        require( [location.protocol + '//' + location.hostname + '/scripts/utils/itunesdetect.js'], function () {
            var url = e.currentTarget.href;
            if ( iTunesDetected() ) {
                itmsOpen( url );
            }
            else {
                window.open( url, '_blank' );
            }
        } );
    } );

    //Close the calendar 1-day details layer
    $( '.calendar-details .close' ).click( function () {
        $( '.calendar-details' ).hide();
    } );

    // cancel click on hidden elements
    $( '#events-calendar' ).on( 'click', '.hidden', function ( e ) {
        e.preventDefault();
    } );

    //Show the "X more" event layer
    $( '.more' ).on( 'click', function ( e ) {
        var args = [],
            selectedEvents = mapInArray( eventCalendarSettings.ALL_EVENT_TYPES, eventCalendarSettings.selectedTypes );

        // assemble current date args from DOM
        args.push( $( '#year' ).text(),
            $( '#month' ).attr( 'title' ),
            $( this ).closest( 'td' ).find( '.date' ).text() );

        GetCalendarOneDay.apply( null, args.concat( selectedEvents ) );

        //Move the layer into position
        $( '.calendar-details' ).css( {'top': $( this ).position().top, 'left': $( this ).position().left} );

        $( '.calendar-details' ).show();
    } );

    // filter event selection
    $( '#Div3, #event-refinements' ).on( 'change', 'input[type="checkbox"]', function ( e ) {
        // reset selectedType
        updateSelectedEventTypes();
        $( document ).trigger( 'update:events' );
    } );

    // Agenda View
    // toggle events for month
    $( '#events-calendar' ).on( 'click', '.agenda .month.link1', function ( e ) {
        $( e.currentTarget ).toggleClass( 'active' ).next().toggleClass( 'hidden' );
    } );

    // update selected event types and assoc count bubbles
    // TOOD: determine view type before execution
    $( document ).on( 'update:events', function () {
        var args = [],
            selectedEvents = mapInArray( eventCalendarSettings.ALL_EVENT_TYPES, eventCalendarSettings.selectedTypes );
        // if calendar view re-render entire view with selected event types
        if ( getParameterByName( 'view' ) == 'calendar' ) {
            // assemble current date args from DOM
            args.push( $( '#year' ).text(), $( '#month' ).attr( 'title' ) );

            // in agenda view, toggle selected items in timeline
            GetCalendar.apply( null, args.concat( selectedEvents ) );
        }
        else {
            filterEvents();
            updateEventCounts();
        }
    } );

    // only initialize if on calendar node exists
    if ( $( '#agendaCalendar' )[0] || $( '#calendarBody' )[0] ) {
        initCalendar();
    }
} );

function initCalendar() {
    var today = new Date,
        selectedEvents;
    // initialize Calendar
    // load view based on today's date
    updateSelectedEventTypes();

    selectedEvents = mapInArray( eventCalendarSettings.ALL_EVENT_TYPES, eventCalendarSettings.selectedTypes );
    if ( getParameterByName( 'view' ) == 'calendar' ) {
        var args = [
            today.getFullYear(), today.getMonth() + 1
        ];
        GetCalendar.apply( null, args.concat( selectedEvents ) );
    }
    else {
        // load all events initially for agenda view, then filter by selected
        GetAgenda( ( new Date ).getFullYear(), true, true, true, true );
        filterEvents();
    }
}

function updateSelectedEventTypes() {
    eventCalendarSettings.selectedTypes = [];
    // update selectedTypes
    $( 'input[id^="calendar-"][type="checkbox"]:checked' ).each( function () {
        eventCalendarSettings.selectedTypes.push( this.value );
    } );
}

// return a list of jQuery class selectors for selected event types
function getSelectedEventSelectorList() {
    var values = [];
    $( 'input[id^="calendar-"][type="checkbox"]:checked' ).each( function () {
        var type;
        switch ( this.id ) {
            case 'calendar-destination':
                type = '.DestinationSeminar';
                break;
            case 'calendar-one-day':
                type = '.RegionalCourse';
                break;
            case 'calendar-free':
                type = '.ComplimentaryEvent';
                break;
            case 'calendar-tradeshow':
                type = '.Tradeshow';
                break;
        }
        values.push( type );
    } );

    return values.join( ',' );
}

/**
 * Loop over src array and determine values in supplied array exist in src array
 * @param  {Array} a source array
 * @param  {Array} b compare array
 * @return {Array}   booleans
 */
function mapInArray( a, b ) {
    var result = [];
    $( a ).each( function ( i, item ) {
        if ( $.inArray( item, b ) > -1 ) {
            result.push( true );
        }
        else {
            result.push( false );
        }
    } );

    return result;
}

// return color class for event ContentType
function getEventTypeClass( contentType ) {
    var type = '';
    switch ( contentType ) {
        case 'Destination Seminar':
            type = 'de';
            break;
        case 'Regional Course':
            type = 'ode';
            break;
        case 'Complimentary Event':
            type = 'fe';
            break;
        default:
            type = 'ts';
    }
    return type;
}

/**
 * getSelectedEventTypeCount { agenda view }
 * get total selected event count for specified month
 * @param{ DOMNode } month: xml node containing list of events for events calendar month
 */
function getSelectedEventTypeCount( events ) {
    var count = 0,
        selectedTypes = eventCalendarSettings.selectedTypes || eventCalendarSettings.ALL_EVENT_TYPES;

    $( events ).each( function ( i, item ) {
        var contentType = $( this ).find( 'ContentType' ).text();
        if ( $.inArray( contentType, selectedTypes ) > -1 ) {
            count++;
        }
    } );

    return count;
}

// TODO: make sure filtering works for toggle between calendar/agenda views
function filterEvents() {
    var $eventList = $( '.events' ),
        allEvents = $eventList.find( '.DestinationSeminar, .RegionalCourse, .ComplimentaryEvent, .Tradeshow' ),
        selectedEventSelectors = getSelectedEventSelectorList();

    // reset view of all events in list
    allEvents.addClass( 'hidden' );
    $eventList.find( selectedEventSelectors ).removeClass( 'hidden' );
}

function updateEventCounts() {
    // get calendar month wrappers
    var $events = $( '#events-calendar ul > li[class^="events-"]' );
    var selectors = $.map( eventCalendarSettings.selectedTypes, function ( item ) {
        return '[data-content-type*="' + item + '"]';
    } ).join( ',' );

    // get selected event counts for each month
    $events.each( function ( i, item ) {
        // search for elements with contentType == settings.selectedTypes
        // if count is > 0 activate else disable
        var $month = $( item );
        var selectedEvents = $month.find( selectors );

        // toggle countBubble visible class
        $month.find( 'h3.month' )[( selectedEvents.length > 0 )
            ? 'addClass'
            : 'removeClass']( 'link1 ' );
        // update countBubble count
        $month.find( '.count' ).text( selectedEvents.length );
    } );
}

/* Shows a tab on the event detail page */
function tabDisplay( id ) {
    $( '.tabNavigation' ).find( 'a' ).parent().attr( 'class', '' );
    $( '.tabNavigation' ).find( 'a:eq(' + $( '#' + id ).index() + ')' ).parent().attr( 'class', 'selected' );
    $( '.tabPanel' ).children().hide();
    $( '#' + id ).show();
}

/* Ajax call to get agenda items */
function GetAgenda( year, destination, regional, complimentary, tradeshow ) {
    //Unbind the next/prev buttons.  We'll reattach them with updated ajax calls.
    $( '#cal-next' ).unbind( 'click' );
    $( '#cal-back' ).unbind( 'click' );

    // refactor: calls to pull all events and toggle selected events accoringly
    $( '#cal-next' ).click( function () {
        GetAgenda( parseInt( $( '#year' ).text() ) + 1, true, true, true, true );
        filterEvents();
    } );

    $( '#cal-back' ).click( function () {
        GetAgenda( parseInt( $( '#year' ).text() ) - 1, true, true, true, true );
        filterEvents();
    } );
    // refactor: END

    EventsService.getCalendar( year, {destination, regional, complimentary, tradeshow} )
        .then( xml => {
            //hide previous button if the current year is the selected year.
            var $calendarXML = $( xml ),
                $calendarInfo = $calendarXML.find( 'Information' ),
                calendarTitle = $calendarInfo.find( 'Title' ).text(),
                calendarYear = $calendarInfo.find( 'Year' ).text(),
                today = new Date;

            if ( today.getFullYear() == calendarTitle ) {
                $( '#cal-back' ).hide();
            }
            else {
                $( '#cal-back' ).show();
            }
            //hide next button if the current year is the selected year + 1.
            if ( today.getFullYear() + 1 == calendarTitle ) {
                $( '#cal-next' ).hide();
            }
            else {
                $( '#cal-next' ).show();
            }

            var eventsHTML = '<h2 class="year" id="year">' + calendarYear + '</h2>';

            $calendarXML.find( 'Month' ).each( function ( i, item ) {

                var $month = $( this );

                function isCurrentMonth() {
                    return ( new Date ).getMonthName().toLowerCase() == $month.attr( 'name' ).toLowerCase();
                };

                // foreach month build a list of events
                var $events = $month.find( 'Event' );
                var selectedEventCount = getSelectedEventTypeCount( $events );
                var countBubble = '<span class="count tag">' + selectedEventCount + '</span>';

                eventsHTML +=
                    '<li class="events-' +
                    $month.attr( 'name' ).toLowerCase() +
                    '"><h3 class="month ' +
                    ( selectedEventCount
                        ? ' link1'
                        : '' ) +
                    ' ' +
                    ( isCurrentMonth()
                        ? 'active'
                        : '' ) +
                    '"><i class="indicator"></i>' +
                    countBubble +
                    $month.attr( 'name' ) +
                    '</h3>';

                if ( $events.length ) {
                    eventsHTML += '<ul class="events ' + ( !isCurrentMonth()
                        ? 'hidden'
                        : '' ) + '">';
                    $events.each( function () {
                        var $event = $( this );
                        var eventType = $event.find( 'ContentType' ).text();
                        var url = $event.find( 'URL' ).text();
                        var $aTag = $( $event.find( 'Link' ).text() );
                        var tradeShowURL = $aTag.attr( 'href' );
                        var href = url || tradeShowURL; // tradeshow urls are handled differently from the rest of the event types
                        var target = $aTag.attr( 'target' ) || '';

                        eventsHTML +=
                            '<li data-content-type="' +
                            eventType +
                            '" class="' +
                            eventType.replace( ' ', '' ) +
                            ' ' +
                            getEventTypeClass( eventType ) +
                            '">';
                        eventsHTML += '<span class="day">' + $event.find( 'StartDay' ).text() + '</span> ';
                        eventsHTML +=
                            '<a class="course" target="' +
                            target +
                            '" href="' +
                            href +
                            '">' +
                            $event.find( 'Title' ).text() +
                            '</a>';

                        // extract a list of speakers for rhis event
                        var $speakers = $event.find( 'Speaker' );
                        $speakers.each( function () {
                            var $speaker = $( this );
                            eventsHTML +=
                                '&nbsp;<a class="speaker" href="continuing-education-speaker-bio.aspx?speakerId=' +
                                $speaker.find( 'ID' ).text() +
                                '">' +
                                $speaker.find( 'Name' ).text() +
                                '</a>';
                            if ( $speakers.length > 1 ) {
                                eventsHTML += ';';
                            }
                        } );

                        eventsHTML +=
                            ' <span class="location">&mdash; ' +
                            $event.find( 'City' ).text() +
                            ', ' +
                            $event.find( 'State' ).text() +
                            ' ' +
                            $event.find( 'Country' ).text() +
                            '</span>';
                        eventsHTML += '</li>';
                    } );
                    eventsHTML += '</ul>';
                }
                eventsHTML += '</li>';
            } );

            $( '#agendaCalendar' ).html( eventsHTML );

            if ( Modernizr && Modernizr.csstransitions ) {
                // prepare classes for csstransitions
                $( '.events' )
                    .addClass( 'page-turn' )
                    .find( '.DestinationSeminar, .RegionalCourse, .ComplimentaryEvent, .Tradeshow' )
                    .addClass( 'page-turn' );
            }

            // display only selected event types in view upon each new request
            $( document ).trigger( 'update:events' );
        } )
        .catch( err => {
            $( '#agendaCalendar' )
                .removeClass( 'loading' )
                .addClass( 'error' )
                .html( 'There was an error while loading this data.' );
        } )

}

/* Ajax call to get calendar items (calendar view) */
function GetCalendar( year, month, destination, regional, complimentary, tradeshow ) {

    //Unbind the next/prev buttons.  We'll reattach them with updated ajax calls.
    $( '#cal-next' ).unbind( 'click' );
    $( '#cal-back' ).unbind( 'click' );
    $( '.calendar-details' ).hide();
    //Hide day detail layer if it is currently displayed.

    var calendarDate = new Date( year, month - 1 );
    var nextMonthDate = new Date( calendarDate.getFullYear(), calendarDate.getMonth() + 1 );
    var prevMonthDate = new Date( calendarDate.getFullYear(), calendarDate.getMonth() - 1 );

    $( '#cal-next' ).click( function () {
        GetCalendar( nextMonthDate.getFullYear(), nextMonthDate.getMonth() + 1,
            $( '#calendar-destination' ).is( ':checked' ),
            $( '#calendar-one-day' ).is( ':checked' ),
            $( '#calendar-free' ).is( ':checked' ),
            $( '#calendar-tradeshow' ).is( ':checked' ) );
    } );

    $( '#cal-back' ).click( function () {
        GetCalendar( prevMonthDate.getFullYear(), prevMonthDate.getMonth() + 1,
            $( '#calendar-destination' ).is( ':checked' ),
            $( '#calendar-one-day' ).is( ':checked' ),
            $( '#calendar-free' ).is( ':checked' ),
            $( '#calendar-tradeshow' ).is( ':checked' ) );
    } );

    EventsService.getCalendar( year, {month, destination, regional, complimentary, tradeshow} )
        .then( xml => {
            var $calendarXML = $( xml );

            $( '.date' )
                .html( '<span id="month" title="' +
                    $calendarXML.find( 'Information' ).find( 'MonthInt' ).text() +
                    '">' +
                    $calendarXML.find( 'Information' ).find( 'Month' ).text() +
                    '</span> <span id="year">' +
                    $calendarXML.find( 'Information' ).find( 'Year' ).text() +
                    '</span>' );

            var eventsHTML = '';

            var firstDay = parseInt( $calendarXML.find( 'Information' ).find( 'First' ).text() );
            var totalDays = parseInt( $calendarXML.find( 'Information' ).find( 'Days' ).text() );
            // determine how many week blocks to render
            var daysOffset = ( firstDay - 1 ) + totalDays;
            var weekBlocks = (Math.floor( daysOffset / 7 ) + ( ( daysOffset % 7 !== 0 ) ? 1 : 0 ));
            var row = 'even';
            for ( var i = 1, l = ( weekBlocks * 7 ); i <= l; i++ ) {
                row = ( i % 2 == 0 ) ? 'even' : 'odd';

                var displayDate = i - ( firstDay - 1 );

                if ( ( i - 1 ) % 7 == 0 ) {
                    eventsHTML += '<tr class="' + row + '">';
                }
                eventsHTML += '<td>';
                eventsHTML += '<span class="date">';
                eventsHTML += ( i >= firstDay && displayDate <= totalDays )
                    ? displayDate
                    : '&nbsp;';
                eventsHTML += '</span>';

                //Add events for the date
                eventsHTML += '<div class="events" id="' + month + '-' + displayDate + '">';
                eventsHTML += '<span class="hidden currentCount empty">0</span>';
                eventsHTML += '<span class="hidden moreCount">0</span>';
                eventsHTML += '<span class="moreLink"></span>';
                eventsHTML += '</div>';

                eventsHTML += '</td>';
                if ( i % 7 == 0 ) {
                    eventsHTML += '</tr>'
                }
            }

            $( '#calendarBody' ).html( eventsHTML );

            var $events = $calendarXML.find( 'Event' );
            if ( $events.length > 0 ) {
                $events.each( function () {
                    var $linkClass = 'ode';
                    switch ( $( this ).find( 'ContentType' ).text() ) {
                        case 'Complimentary Event':
                            $linkClass = 'fe';
                            break;
                        case 'Regional Course':
                            $linkClass = 'ode';
                            break;
                        case 'Destination Seminar':
                            $linkClass = 'de';
                            break;
                        case 'Tradeshow':
                            $linkClass = 'ts';
                            break;
                    }

                    // Loop through the date range of each event and add it to the calendar or to the 'more' section.
                    var loopDate = 1;
                    var datFrom = new Date( $( this ).find( 'StartDate' ).text() );
                    //if the event start date is a previous month, set the start date to the beginning of the displayed month (less looping).
                    if ( datFrom.getMonth() + 1 < month ) {
                        datFrom = new Date( year, month - 1, 1 );
                    }
                    var datTo = new Date( $( this ).find( 'EndDate' ).text() );
                    //if the event end date is after the currently displayed month, set the end date to the end of the displayed month (less looping).
                    if ( datTo.getMonth() + 1 > month ) {
                        datTo = new Date( year, month - 1, totalDays );
                    }
                    loopDate = datFrom.getDate();

                    while ( loopDate <= datTo.getDate() ) {
                        var cellID = month + '-' + loopDate;
                        var $current = $( '#' + cellID ).find( '.currentCount' );
                        var currentCount = parseInt( $current.text() );
                        var $more = $( '#' + cellID ).find( '.moreCount' );
                        var moreCount = parseInt( $more.text() );
                        var url = $( this ).find( 'URL' ).text();
                        var tradeShowURL = $( $( this ).find( 'Link' ).text() ).attr( 'href' );
                        var href = url || tradeShowURL;
                        var target = $( $( this ).find( 'Link' ).text() ).attr( 'target' ) == undefined
                            ? ''
                            : $( $( this ).find( 'Link' ).text() ).attr( 'target' );

                        if ( currentCount == 0 ) {
                            $( '<a class="' +
                                $linkClass +
                                ' ' +
                                $( this ).find( 'ContentType' ).text().replace( ' ', '' ) +
                                '" target="' +
                                target +
                                '" href="' +
                                href +
                                '">' +
                                $( this ).find( 'Title' ).text() +
                                ' &raquo;</a>' ).insertBefore( $( '#' + cellID + ' .empty' ) );
                            $current.text( currentCount + 1 );
                        }
                        else {
                            $more.text( moreCount + 1 );
                            $( '#' + cellID + ' .moreLink' )
                                .html( '<a class="more events" style="cursor:pointer;" >' +
                                    parseInt( $more.text() ) +
                                    ' more</a>' );
                        }
                        loopDate = loopDate + 1;
                    }
                } );
            }
        } )
        .catch( err => {
            $( '#calendarBody' )
                .removeClass( 'loading' )
                .addClass( 'error' )
                .html( 'There was an error while loading this data.' );
        } );
}

/* Ajax call to get calendar items for one day layer (calendar view) */
function GetCalendarOneDay( year, month, day, destination, regional, complimentary, tradeshow ) {
    EventsService.getCalendar( year, {month, day, destination, regional, complimentary, tradeshow} )
        .then( xml => {
            var $calendarXML = $( xml );

            $( '.calendar-details .date' )
                .html( $calendarXML.find( 'Information' ).find( 'Month' ).text() + ' ' + day + ', ' + year );

            var eventsHTML = '';
            var $events = $calendarXML.find( 'Event' ).each( function () {
                var $linkClass = 'ode';
                switch ( $( this ).find( 'ContentType' ).text() ) {
                    case 'Complimentary Event':
                        $linkClass = 'fe';
                        break;
                    case 'Regional Course':
                        $linkClass = 'ode';
                        break;
                    case 'Destination Seminar':
                        $linkClass = 'de';
                        break;
                    case 'Tradeshow':
                        $linkClass = 'ts';
                        break;
                }
                var url = $( this ).find( 'URL' ).text();
                var tradeShowURL = $( $( this ).find( 'Link' ).text() ).attr( 'href' );
                var href = url || tradeShowURL;
                var target = $( $( this ).find( 'Link' ).text() ).attr( 'target' ) == undefined
                    ? ''
                    : $( $( this ).find( 'Link' ).text() ).attr( 'target' );

                eventsHTML +=
                    '<li><a class="' +
                    $linkClass +
                    '" target="' +
                    target +
                    '" href="' +
                    href +
                    '">' +
                    $( this ).find( 'Title' ).text() +
                    ' &raquo;</a></li>';
            } );

            $( '.calendar-details .events' ).html( eventsHTML );
        } )
        .catch( err => {
            $( '.calendar-details .events' )
                .removeClass( 'loading' )
                .addClass( 'error' )
                .append( 'There was an error while loading this data.' );
        } );

}

window.tabDisplay = tabDisplay;



