import ServiceProvider from '../modules/core/ServiceProvider';

const svc = new ServiceProvider( {
    baseURL: upi.conf.svc.EVENTS_API,
    responseType: 'text'
} );

const EventsService = {

    getCalendar( year, params ) {
        if ( !year || !/^\d{4}$/.test( year ) ) {
            throw new SyntaxError( 'Expects first param to be a valid 4 digit year' );
        }

        return svc.request( `/GetCalendar/${year}`, {params} )
            .then( ServiceProvider.responseData );
    }

};

export default EventsService;














